import axios from 'axios';

export const userConstants = {
  LAUNCH_YEAR_PENDING :'LAUNCH_YEAR_PENDING',
  LAUNCH_YEAR_SUCCESS : 'LAUNCH_YEAR_SUCCESS',
  LAUNCH_YEAR_ERROR : 'LAUNCH_YEAR_ERROR',
}
  export const userActions = {
    LaunchYearDetails
    };

function LaunchYearDetails(landing_success, launch_success, launch_year) {
  return dispatch => {
    console.log('launch_year-action--', launch_year );
    console.log('launch_success-action--', launch_success );
    console.log('landing_success-action--', landing_success );
      dispatch(request(launch_year));
      return axios.get('https://api.spaceXdata.com/v3/launches?limit=100&launch_success='+ launch_success + '&land_success='+ landing_success + '&launch_year='+ launch_year)
          .then(
            res => { 
                dispatch(success(res));
                //history.push(`/admin/projects/${studyName}/false`);
              },
              error => {
                //history.push(`/admin/projects/${studyName}/false`);
            }
          );
  };
function request(res) { return { type: userConstants.LAUNCH_YEAR_PENDING, res } }
function success(res) { return { type: userConstants.LAUNCH_YEAR_SUCCESS, res } }
function failure(error) { return { type: userConstants.LAUNCH_YEAR_ERROR, error } }
}


