export const userConstants = {
  LAUNCH_YEAR_SUCCESS: 'LAUNCH_YEAR_SUCCESS',
  LAUNCH_YEAR_ERROR: 'LAUNCH_YEAR_ERROR',
};

const initialState = {
  projectList:{}
};

export function filterReducer(state = initialState, action) {
  switch(action.type) {
      case userConstants.LAUNCH_YEAR_SUCCESS:
              console.log('sucesss', action.res.data)
              return {...state, projectList: action.res.data}
          
      case userConstants.LAUNCH_YEAR_ERROR:
          return {
              ...state,
              error: action.error
          }
      default: 
          return state;
  }
}

export const getProducts = state => state.products;
export const getProductsPending = state => state.pending;
export const getProductsError = state => state.error;
